import React, { Fragment, Component } from "react";
import { connect } from "react-redux";
import { Button, withStyles, TextField } from "@material-ui/core";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";

const styles = () => ({
  container: {
    display: "flex",
    flexWrap: "wrap"
  },
  textField: {
    marginLeft: 100,
    marginRight: 100,
    width: 200
  },
  menu: {
    width: 200
  }
});

class Calculator extends Component {
  state = {
    openCur: false,
    openRate: false,
    openCalc: false,
    currency: {
      currencyName: "",
      value: ""
    },
    rateName: "",
    rateValue: 0.0,
    amount: 0.0
  };

  openCalc = () => {
    this.setState({ openCalc: !this.state.openCalc });
  };

  handleCurrencyToggle = () => {
    this.setState({ openCur: !this.state.openCur });
  };

  handleRateToggle = () => {
    this.setState({ openRate: !this.state.openRate });
  };

  handleCurrencyChoise = currency => {
    console.log(currency.value);
    currency.value = parseFloat(currency.value);
    this.setState({ currency });
    this.handleCurrencyToggle();
  };

  handleRateChoise = rateAction => {
    const { currency } = this.state;
    if (currency.currencyName !== "") {
      this.props.rates.map(rate => {
        if (rate.currencyName === currency.currencyName) {
          this.setState({
            rateValue: rate[rateAction],
            rateName: rateAction
          });
        }
      });
    }
    this.handleRateToggle();
    return;
  };

  handleChange = ({ target: input }) => {
    this.setState({
      amount: input.value
    });
  };

  render() {
    const { classes, currencies } = this.props;
    const {
      openCalc,
      openCur,
      openRate,
      currency,
      amount,
      rateName,
      rateValue
    } = this.state; // if the page doesnt render properly we should add loadedRates...
    return (
      <Fragment>
        <Button onClick={() => this.openCalc()}>Calculator</Button>
        {openCalc && (
          <Fragment>
            <Button
              aria-controls="simple-menu"
              aria-haspopup="true"
              onClick={() => this.handleCurrencyToggle()}
            >
              {currency.currencyName || "currency"}
            </Button>
            <Menu
              id="simple-menu"
              open={openCur}
              onClose={() => this.handleCurrencyToggle()}
            >
              {currencies.map(currency => (
                <MenuItem
                  key={currency.currencyName}
                  onClick={() => this.handleCurrencyChoise(currency)}
                >
                  {currency.currencyName}
                </MenuItem>
              ))}
            </Menu>

            <Button
              aria-controls="simple-menu-2"
              aria-haspopup="true"
              onClick={() => this.handleRateToggle()}
            >
              {rateName || "action"}
            </Button>
            <Menu
              id="simple-menu-2"
              open={openRate}
              onClose={() => this.handleRateToggle()}
            >
              <MenuItem onClick={() => this.handleRateChoise("buyCashRate")}>
                buy cash
              </MenuItem>
              <MenuItem onClick={() => this.handleRateChoise("sellCashRate")}>
                sell cash
              </MenuItem>
              <MenuItem
                onClick={() => this.handleRateChoise("buyTransferRate")}
              >
                buy by transfer
              </MenuItem>
              <MenuItem
                onClick={() => this.handleRateChoise("sellTransferRate")}
              >
                sell by transfer
              </MenuItem>
            </Menu>
            <TextField
              className={classes.textField}
              autoFocus
              name="amount"
              label="amount"
              type="amount"
              value={amount}
              onChange={this.handleChange}
              fullWidth
              required
            />
            <div>{(Number(currency.value) + rateValue) * amount}</div>
          </Fragment>
        )}
      </Fragment>
    );
  }
}

const mapStateToProps = state => {
  return {
    rates: state.getRatesReducer.rates,
    currencies: state.getCurrenciesReducer.currencies
  };
};

export default connect(mapStateToProps, null)(withStyles(styles)(Calculator));
