import { toast } from "react-toastify";
import {
  UPDATE_RATE_SUCCESS,
  UPDATE_RATE_FAIL,
  UPDATE_RATE_REQUEST,
  LOAD_UPDATE_RATE_FORM,
  FINISH_UPDATE
} from "../../../Actions/MarketDataActions/Rates/updateRateActions";

const initialState = { desiredRate: {}, updateRateSuccess: false };

export default function(state = initialState, action) {
  switch (action.type) {
    case LOAD_UPDATE_RATE_FORM:
      return {
        ...state,
        updateRateSuccess: false,
        desiredRate: action.payload.rate
      };
    case UPDATE_RATE_REQUEST:
      return { ...state, updateRateSuccess: false };
    case UPDATE_RATE_SUCCESS:
      return { ...state, updateRateSuccess: true };
    case UPDATE_RATE_FAIL:
      toast("failed to update rate");
      return { ...state, updateRateSuccess: false };
    case FINISH_UPDATE:
      return { ...state, updateRateSuccess: false };
    default:
      return state;
  }
}
