import React, { Component, Fragment } from "react";
import { Route, Redirect, Switch } from "react-router-dom";
import LoginPage from "./Components/Users/loginPage";
import AdminPage from "./Components/Admin/adminPage";
import RegisterPage from "./Components/Admin/registerPage";
import UserPage from "./Components/Users/userPage";
import UpdatePage from "./Components/Users/updatePage";
import UpdateRate from "./Components/MarketData/Rates/updateRate";
import RateManagement from "./Components/MarketData/Rates/rateManagement";
import AddRatePage from "./Components/MarketData/Rates/addRatePage";
import ForgotPasswordPage from "./Components/Users/forgotPasswordPage";
import { connect } from "react-redux";
import { autoLogin } from "./Redux/Actions/UserActions/userActions";

class App extends Component {
  render() {
    return (
      <Fragment>
        <Switch>
          <Route path="/adminPage" component={AdminPage} />
          <Route path="/registerPage" component={RegisterPage} />
          <Route path="/forgotPasswordPage" component={ForgotPasswordPage} />
          <Route path="/userPage" component={UserPage} />
          <Route path="/addRatePage" component={AddRatePage} />
          <Route path="/rateManagement" component={RateManagement} />
          <Route path="/updatePage" component={UpdatePage} />
          <Route path="/updateRate" component={UpdateRate} />
          <Route path="/loginPage" component={LoginPage} />
          <Route exact path="/" render={() => <Redirect to="/loginPage" />} />
          <Route path="*" render={() => <Redirect to="/loginPage" />} />
        </Switch>
      </Fragment>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  autoLogin: () => dispatch(autoLogin())
});

const mapStateToProps = state => {
  return {
    LoggedIn: state.userReducer.LoggedIn,
    user: state.userReducer.user
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
