import React, { Fragment, Component } from "react";
import { connect } from "react-redux";
import { forgotPasswordRequest } from "../../Redux/Actions/UserActions/forgotPasswordActions";
import { TextField, Button, withStyles } from "@material-ui/core";
import PropTypes from "prop-types";

const styles = () => ({
  container: {
    display: "flex",
    flexWrap: "wrap"
  },
  textField: {
    marginLeft: 100,
    marginRight: 100,
    width: 200
  },
  menu: {
    width: 200
  }
});

class ForgotPasswordPage extends Component {
  state = {
    data: {
      email: ""
    }
  };

  componentDidMount() {
    if (this.props.LoggedIn === true) {
      this.props.history.push("/userPage");
    }
  }

  handleChange = ({ target: input }) => {
    this.setState({
      data: {
        ...this.state.data,
        [input.name]: input.value
      }
    });
  };

  submit(e) {
    e.preventDefault();
    const { data } = this.state;
    this.props.forgotPasswordRequest(data.email);
    this.props.history.push("/loginPage");
  }

  renderInput(name) {
    const classes = this.props;
    return (
      <TextField
        className={classes.textField}
        autoFocus
        name={name}
        label={name}
        type={name}
        value={this.state.data[name]}
        onChange={this.handleChange}
        required
      />
    );
  }

  render() {
    const classes = this.props;
    return (
      <Fragment>
        <form
          className={classes.container}
          onSubmit={this.submit.bind(this)}
          autoComplete="off"
        >
          <div>{this.renderInput("email")}</div>
          <Button type="submit" variant="outlined">
            reset password
          </Button>
        </form>

        <Button
          variant="outlined"
          onClick={() => {
            this.props.history.push("/loginPage");
          }}
        >
          cancel
        </Button>
      </Fragment>
    );
  }
}

ForgotPasswordPage.propTypes = {
  classes: PropTypes.object.isRequired
};

const mapDispatchToProps = dispatch => ({
  forgotPasswordRequest: email => dispatch(forgotPasswordRequest(email))
});

const mapStateToProps = state => {
  return {
    LoggedIn: state.userReducer.LoggedIn
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(ForgotPasswordPage));
