export const USER_LOGIN_REQUEST = "USER_LOGIN_REQUEST";
export const USER_LOGIN_SUCCESS = "USER_LOGIN_SUCCESS";
export const USER_LOGIN_FAIL = "USER_LOGIN_FAIL";

export const USER_LOGOUT_REQUEST = "USER_LOGOUT_REQUEST";
export const USER_LOGOUT_SUCCESS = "USER_LOGOUT_SUCCESS";

export const USER_LOAD_FAIL = "USER_LOAD_FAIL";
export const USER_LOAD_REQUEST = "USER_LOAD_REQUEST";
export const USER_LOAD_SUCCESS = "USER_LOAD_SUCCESS";

export const AUTOLOGIN = "AUTOLOGIN";
export const AUTOLOGIN_FAIL = "AUTOLOGIN_FAIL";
export const AUTOLOGIN_SUCCESS = "AUTOLOGIN_SUCCESS";

export const userLoginRequest = user => ({
  type: USER_LOGIN_REQUEST,
  payload: user
});

export const userLoginSuccess = user => ({
  type: USER_LOGIN_SUCCESS,
  payload: user
});

export const userLoginFail = error => ({
  type: USER_LOGIN_FAIL,
  payload: error
});

export const userLoadRequest = () => ({
  type: USER_LOAD_REQUEST
});

export const userLoadSuccess = user => ({
  type: USER_LOAD_SUCCESS,
  payload: user
});

export const userLoadFail = error => ({
  type: USER_LOAD_FAIL,
  payload: error
});

export const logoutRequest = () => ({
  type: USER_LOGOUT_REQUEST
});

export const logoutSuccess = () => ({
  type: USER_LOGOUT_SUCCESS
});

export const autoLogin = () => ({
  type: AUTOLOGIN
});

export const autoLoginSuccess = isLoggedIn => ({
  type: AUTOLOGIN_SUCCESS,
  payload: isLoggedIn
});

export const autoLoginFail = error => ({
  type: AUTOLOGIN_FAIL,
  payload: error
});
