import {
  USER_LOGIN_SUCCESS,
  USER_LOGIN_FAIL,
  USER_LOGIN_REQUEST,
  USER_LOGOUT_REQUEST,
  USER_LOGOUT_SUCCESS,
  USER_LOAD_FAIL,
  USER_LOAD_REQUEST,
  USER_LOAD_SUCCESS,
  AUTOLOGIN,
  AUTOLOGIN_FAIL,
  AUTOLOGIN_SUCCESS
} from "../../Actions/UserActions/userActions";

const initialState = {
  user: {},
  loadedUser: false,
  LoggedIn: false,
  loadLoggedIn: true
};

export default function(state = initialState, action) {
  switch (action.type) {
    case AUTOLOGIN:
      return {
        ...state,
        loadLoggedIn: true
      };
    case AUTOLOGIN_SUCCESS:
      if (action.payload === true) {
        return {
          ...state,
          LoggedIn: true,
          loadLoggedIn: false
        };
      } else {
        return {
          ...state,
          LoggedIn: false,
          loadLoggedIn: false
        };
      }
    case AUTOLOGIN_FAIL:
      return { ...state, LoggedIn: false, loadLoggedIn: false };

    case USER_LOGIN_REQUEST:
      return { ...state, loadLoggedIn: false };
    case USER_LOGIN_SUCCESS:
      return {
        ...state,
        LoggedIn: true,
        loadLoggedIn: false
      };
    case USER_LOGIN_FAIL:
      return { ...state, LoggedIn: false };

    case USER_LOGOUT_REQUEST:
      return {};
    case USER_LOGOUT_SUCCESS:
      return {};

    case USER_LOAD_REQUEST:
      return { ...state, loadedUser: false };
    case USER_LOAD_SUCCESS:
      return { ...state, loadedUser: true, user: { ...action.payload } };
    case USER_LOAD_FAIL:
      return { ...state, loadedUser: false, LoggedIn: false };

    default:
      return state;
  }
}
