import React, { Fragment, Component } from "react";
import PropTypes from "prop-types";
import { withStyles, Button, TextField } from "@material-ui/core";
import { registerUserRequest } from "../../Redux/Actions/AdminActions/registerUserActions";
import { connect } from "react-redux";

const styles = {
  root: {
    margin: "20px"
  }
};

class RegisterPage extends Component {
  state = {
    data: {
      name: "",
      email: "",
      password: "",
      storeName: "",
      address: "",
      licenceNumber: ""
    }
  };

  componentDidMount() {
    if (this.props.LoggedIn) {
      if (!this.props.user.isAdmin) {
        this.props.history.push("/userPage");
      }
    } else {
      this.props.history.push("/loginPage");
    }
  }

  componentDidUpdate() {
    if (this.props.registerUserSuccess === true) {
      this.props.history.push("/adminPage");
    }
  }

  handleChange = ({ target: input }) => {
    this.setState({
      data: {
        ...this.state.data,
        [input.name]: input.value
      }
    });
  };

  renderInput(name) {
    const classes = this.props;
    return (
      <TextField
        className={classes.textField}
        autoFocus
        name={name}
        label={name}
        type={name}
        value={this.state.data[name]}
        onChange={this.handleChange}
        fullWidth
        required
      />
    );
  }

  submit = e => {
    this.props.registerUserRequest(this.state.data);
    e.preventDefault();
  };

  render() {
    const { classes } = this.props;
    return (
      <Fragment>
        <form
          className={classes.container}
          onSubmit={this.submit.bind(this)}
          autoComplete="off"
        >
          {this.renderInput("name")}
          {this.renderInput("email")}
          {this.renderInput("password")}
          {this.renderInput("storeName")}
          {this.renderInput("address")}
          {this.renderInput("licenceNumber")}
          {this.renderInput("ipAdrress")}

          <Button type="submit" color="primary" variant="outlined">
            Register
          </Button>
          <Button
            type="button"
            color="primary"
            variant="outlined"
            onClick={() => {
              this.props.history.push("/adminPage");
            }}
          >
            Cancel
          </Button>
        </form>
      </Fragment>
    );
  }
}

RegisterPage.propTypes = {
  classes: PropTypes.object.isRequired
};

const mapDispatchToProps = dispatch => ({
  registerUserRequest: user => dispatch(registerUserRequest(user))
});

const mapStateToProps = state => {
  return {
    LoggedIn: state.userReducer.LoggedIn,
    user: state.userReducer.user,
    registerUserSuccess: state.registerReducer.registerUserSuccess
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(RegisterPage));
