import React, { Fragment, Component } from "react";
import PropTypes from "prop-types";
import { withStyles, Button, TextField } from "@material-ui/core";
import { connect } from "react-redux";
import { updateRateRequest } from "../../../Redux/Actions/MarketDataActions/Rates/updateRateActions";

const styles = {
  root: {
    margin: "20px"
  }
};

class UpdateRate extends Component {
  state = {
    rate: {
      currencyName: "",
      buyCashRate: 0,
      sellCashRate: 0,
      buyTransferRate: 0,
      sellTransferRate: 0,
      isInTable: false
    },
    data: {
      changeBuyCashRate: false,
      changeSellCashRate: false,
      changeBuyTransferRate: false,
      changeSellTransferRate: false
    }
  };

  componentDidMount() {
    if (this.props.LoggedIn) {
      const rate = this.props.desiredRate;
      this.setState({ rate });
    } else {
      this.props.history.push("/loginPage");
    }
  }

  componentDidUpdate() {
    if (this.props.updateRateSuccess === true) {
      this.props.history.push("/rateManagement");
    }
  }

  changeInput = inputName => {
    this.setState({
      data: {
        ...this.state.data,
        [inputName]: !this.state.data[inputName]
      }
    });
  };

  handleChange = ({ target: input }) => {
    this.setState({
      rate: {
        ...this.state.rate,
        [input.name]: input.value
      }
    });
  };

  renderInput(name) {
    const classes = this.props;

    return (
      <TextField
        className={classes.textField}
        autoFocus
        name={name}
        label={name}
        type={name}
        value={this.state.rate[name]}
        onChange={this.handleChange}
        fullWidth
        required
      />
    );
  }

  submit = e => {
    this.props.updateRateRequest(this.state.rate);
    e.preventDefault();
  };

  render() {
    const { classes } = this.props;
    const { data, rate } = this.state;
    return (
      <Fragment>
        <div>{rate.currencyName}</div>
        <form
          className={classes.container}
          onSubmit={this.submit.bind(this)}
          autoComplete="off"
        >
          <div>
            <Button onClick={() => this.changeInput("changeBuyCashRate")}>
              change BuyCashRate
            </Button>
            {data.changeBuyCashRate && this.renderInput("buyCashRate")}
          </div>
          <div>
            <Button onClick={() => this.changeInput("changeSellCashRate")}>
              change SellCashRate
            </Button>
            {data.changeSellCashRate && (
              <div>
                <div>{this.renderInput("sellCashRate")}</div>
              </div>
            )}
          </div>
          <div>
            <Button onClick={() => this.changeInput("changeBuyTransferRate")}>
              change BuyTransferRate
            </Button>
            {data.changeBuyTransferRate && this.renderInput("buyTransferRate")}
          </div>
          <div>
            <Button onClick={() => this.changeInput("changeSellTransferRate")}>
              change SellTransferRate
            </Button>
            {data.changeSellTransferRate &&
              this.renderInput("sellTransferRate")}
          </div>
          <Button type="submit" color="primary" variant="outlined">
            Update
          </Button>
          <Button
            type="button"
            color="primary"
            variant="outlined"
            onClick={() => {
              this.props.history.push("/rateManagement");
            }}
          >
            Cancel
          </Button>
        </form>
      </Fragment>
    );
  }
}

UpdateRate.propTypes = {
  classes: PropTypes.object.isRequired
};

const mapDispatchToProps = dispatch => ({
  updateRateRequest: rate => dispatch(updateRateRequest(rate))
});

const mapStateToProps = state => {
  return {
    LoggedIn: state.userReducer.LoggedIn,
    desiredRate: state.updateRateReducer.desiredRate,
    updateRateSuccess: state.updateRateReducer.updateRateSuccess
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(UpdateRate));
