import React, { Fragment, Component } from "react";
import { connect } from "react-redux";
import { Button, withStyles } from "@material-ui/core";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { getUsersRequest } from "../../Redux/Actions/AdminActions/getUsersActions";
import {
  deleteUserRequest,
  setDeleteProps
} from "../../Redux/Actions/AdminActions/deleteUserActions";
import { loadRegisterForm } from "../../Redux/Actions/AdminActions/registerUserActions";
import { loadUpdateForm } from "../../Redux/Actions/AdminActions/updateUserActions";
import MenuItem from "@material-ui/core/MenuItem";
import { Link } from "react-router-dom";

const styles = () => ({
  container: {
    display: "flex",
    flexWrap: "wrap"
  },
  textField: {
    marginLeft: 100,
    marginRight: 100,
    width: 200
  },
  menu: {
    width: 200
  }
});

class AdminPage extends Component {
  componentDidMount() {
    if (this.props.LoggedIn) {
      if (this.props.user.isAdmin) {
        this.props.getUsersRequest();
      } else {
        this.props.history.push("/userPage");
      }
    } else {
      this.props.history.push("/loginPage");
    }
  }

  componentDidUpdate() {
    if (this.props.deleteUserSuccess === true) {
      this.props.getUsersRequest();
      this.props.setDeleteProps();
    }
  }

  handleRegister = () => {
    this.props.loadRegisterForm();
    this.props.history.push("/registerPage");
  };

  handleUpdate = user => {
    this.props.loadUpdateForm(user);
    this.props.history.push("/updatePage");
  };

  handleDelete = id => {
    this.props.deleteUserRequest(id);
  };

  render() {
    const { isLoaded, users } = this.props;
    return (
      <Fragment>
        <ToastContainer />
        <Button onClick={this.handleRegister}>add user</Button>
        <MenuItem component={Link} to={"/userPage"}>
          my user page
        </MenuItem>
        <table>
          <tbody>
            {isLoaded &&
              users.map(user => (
                <tr className="col col-lg-4 grid-wrapper" key={user._id}>
                  <td> {user.email}</td>
                  <td> {user._id}</td>
                  <td>
                    <Button onClick={() => this.handleDelete(user._id)}>
                      delete
                    </Button>
                  </td>
                  <td>
                    <Button onClick={() => this.handleUpdate(user)}>
                      update
                    </Button>
                  </td>
                </tr>
              ))}
          </tbody>
        </table>
      </Fragment>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  getUsersRequest: () => dispatch(getUsersRequest()),
  deleteUserRequest: id => dispatch(deleteUserRequest(id)),
  loadUpdateForm: user => dispatch(loadUpdateForm(user)),
  setDeleteProps: () => dispatch(setDeleteProps()),
  loadRegisterForm: () => dispatch(loadRegisterForm())
});

const mapStateToProps = state => {
  return {
    users: state.getUsersReducer.users,
    isLoaded: state.getUsersReducer.isLoaded,
    LoggedIn: state.userReducer.LoggedIn,
    user: state.userReducer.user,
    deleteUserSuccess: state.deleteUserReducer.deleteUserSuccess
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(AdminPage));
