import { toast } from "react-toastify";
import {
  GET_USERS_SUCCESS,
  GET_USERS_FAIL,
  GET_USERS_REQUEST
} from "../../Actions/AdminActions/getUsersActions";

const initialState = { isLoaded: false, users: [] };

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_USERS_REQUEST:
      return {};
    case GET_USERS_SUCCESS:
      return { ...state, users: [...action.payload], isLoaded: true };
    case GET_USERS_FAIL:
      toast("failed to upload users");
      return {};
    default:
      return state;
  }
}
