import { toast } from "react-toastify";
import {
  RATES_LOAD_REQUEST,
  RATES_LOAD_SUCCESS,
  RATES_LOAD_FAIL
} from "../../../Actions/MarketDataActions/Rates/getRatesActions";

const initialState = {
  rates: [],
  loadedRates: false
};

export default function(state = initialState, action) {
  switch (action.type) {
    case RATES_LOAD_REQUEST:
      return { ...state, loadedRates: false };
    case RATES_LOAD_SUCCESS:
      return { ...state, loadedRates: true, rates: [...action.payload] };
    case RATES_LOAD_FAIL:
      toast("failed to upload user rates");
      return { ...state, loadedRates: false };
    default:
      return state;
  }
}
