import { createStore, applyMiddleware } from "redux";
import createSagaMiddleware from "redux-saga";
import rootReducer from "../Reducers/rootReducer";
import rootSaga from "../Saga/rootSaga";
import { composeWithDevTools } from "redux-devtools-extension/logOnlyInProduction";
import { createWebsocketMiddleware } from "redux-websocket-middleware";

const configureStore = () => {
  const sagaMiddleware = createSagaMiddleware();
  const socketMiddleware = createWebsocketMiddleware({
    // defaultEndpoint: "wss://localhost:3002",
    // transport: "websocket"
  });

  const composeEnhancers = composeWithDevTools({
    realtime: true,
    name: "money exchange",
    hostname: "https://localhost/",
    port: 3002
  });

  const store = createStore(
    rootReducer,
    composeEnhancers(applyMiddleware(socketMiddleware, sagaMiddleware))
  );
  sagaMiddleware.run(rootSaga);
  return store;
};

export default configureStore;
