import React, { Fragment, Component } from "react";
import PropTypes from "prop-types";
import { withStyles, Button, TextField } from "@material-ui/core";
import { connect } from "react-redux";
import { updateUserRequest } from "../../Redux/Actions/AdminActions/updateUserActions";

const styles = {
  root: {
    margin: "20px"
  }
};

class UpdatePage extends Component {
  state = {
    user: {
      name: "",
      email: "",
      password: "",
      newPassword: "",
      storeName: "",
      address: "",
      licenceNumber: ""
    },
    data: {
      changeName: false,
      changeEmail: false,
      changePassword: false,
      changeStoreName: false,
      changeAddress: false,
      changeLicenceNumber: false
    }
  };

  componentDidMount() {
    if (this.props.LoggedIn) {
      let user = this.props.desiredUser;
      user.password = "";
      user.newPassword = "";
      this.setState({ user });
    } else {
      this.props.history.push("/loginPage");
    }
  }

  componentDidUpdate() {
    if (this.props.updateSuccess === true) {
      this.props.history.push("/userPage");
    }
  }

  changeInput = inputName => {
    this.setState({
      data: {
        ...this.state.data,
        [inputName]: !this.state.data[inputName]
      }
    });
  };

  handleChange = ({ target: input }) => {
    this.setState({
      user: {
        ...this.state.user,
        [input.name]: input.value
      }
    });
  };

  renderInput(name) {
    const classes = this.props;

    return (
      <TextField
        className={classes.textField}
        autoFocus
        name={name}
        label={name}
        type={name}
        value={this.state.user[name]}
        onChange={this.handleChange}
        fullWidth
        required
      />
    );
  }

  submit = e => {
    this.props.updateUserRequest(this.state.user);
    e.preventDefault();
  };

  render() {
    const { classes } = this.props;
    const { data } = this.state;
    return (
      <Fragment>
        <form
          className={classes.container}
          onSubmit={this.submit.bind(this)}
          autoComplete="off"
        >
          <div>
            <Button onClick={() => this.changeInput("changeName")}>
              Change Name
            </Button>
            {data.changeName && this.renderInput("name")}
          </div>
          <div>
            <Button onClick={() => this.changeInput("changeEmail")}>
              Change Email
            </Button>
            {data.changeEmail && this.renderInput("email")}
          </div>
          <div>
            <Button onClick={() => this.changeInput("changePassword")}>
              Change Password
            </Button>
            {data.changePassword && (
              <div>
                <div>{this.renderInput("newPassword")}</div>
              </div>
            )}
          </div>
          <div>
            <Button onClick={() => this.changeInput("changeStoreName")}>
              Change store name
            </Button>
            {data.changeStoreName && this.renderInput("storeName")}
          </div>
          <div>
            <Button onClick={() => this.changeInput("changeAddress")}>
              Change Address
            </Button>
            {data.changeAddress && this.renderInput("address")}
          </div>
          <div>
            <Button onClick={() => this.changeInput("changeLicenceNumber")}>
              Change licence number
            </Button>
            {data.changeLicenceNumber && this.renderInput("licenceNumber")}
          </div>
          <span>confirm changes :</span>
          <div>{this.renderInput("password")}</div>
          <Button type="submit" color="primary" variant="outlined">
            Update
          </Button>
          <Button
            type="button"
            color="primary"
            variant="outlined"
            onClick={() => {
              this.props.history.push("/userPage");
            }}
          >
            Cancel
          </Button>
        </form>
      </Fragment>
    );
  }
}

UpdatePage.propTypes = {
  classes: PropTypes.object.isRequired
};

const mapDispatchToProps = dispatch => ({
  updateUserRequest: user => dispatch(updateUserRequest(user))
});

const mapStateToProps = state => {
  return {
    LoggedIn: state.userReducer.LoggedIn,
    desiredUser: state.updateUserReducer.desiredUser,
    updateSuccess: state.updateUserReducer.updateSuccess
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(UpdatePage));
