import React, { Fragment, Component } from "react";
import { connect } from "react-redux";
import {
  userLoginRequest,
  autoLogin
} from "../../Redux/Actions/UserActions/userActions";
import { TextField, Button, withStyles } from "@material-ui/core";
import PropTypes from "prop-types";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const styles = () => ({
  container: {
    display: "flex",
    flexWrap: "wrap"
  },
  textField: {
    marginLeft: 100,
    marginRight: 100,
    width: 200
  },
  menu: {
    width: 200
  }
});

class LoginPage extends Component {
  state = {
    data: {
      email: "",
      password: ""
    }
  };

  componentDidMount() {
    this.props.autoLogin();
  }

  componentDidUpdate() {
    if (this.props.LoggedIn === true) {
      this.props.history.push("/userPage");
    }
  }

  handleChange = ({ target: input }) => {
    this.setState({
      data: {
        ...this.state.data,
        [input.name]: input.value
      }
    });
  };

  handleForgotPassword = () => {
    this.props.history.push("/forgotPasswordPage");
  };

  submit(e) {
    e.preventDefault();
    const { data } = this.state;
    this.props.userLoginRequest(data);
  }

  renderInput(name) {
    const classes = this.props;
    return (
      <TextField
        className={classes.textField}
        autoFocus
        name={name}
        label={name}
        type={name}
        value={this.state.data[name]}
        onChange={this.handleChange}
        required
      />
    );
  }

  render() {
    const { classes, loadLoggedIn } = this.props;
    return (
      <Fragment>
        <ToastContainer />
        {!loadLoggedIn && (
          <Fragment>
            <form
              className={classes.container}
              onSubmit={this.submit.bind(this)}
              autoComplete="off"
            >
              <div>{this.renderInput("email")}</div>
              <div>{this.renderInput("password")}</div>
              <Button type="submit" variant="outlined">
                Login
              </Button>
            </form>
            <Button
              type="button"
              variant="outlined"
              onClick={this.handleForgotPassword}
            >
              forgot password
            </Button>
          </Fragment>
        )}
      </Fragment>
    );
  }
}

LoginPage.propTypes = {
  classes: PropTypes.object.isRequired
};

const mapDispatchToProps = dispatch => ({
  userLoginRequest: user => dispatch(userLoginRequest(user)),
  autoLogin: () => dispatch(autoLogin())
});

const mapStateToProps = state => {
  return {
    LoggedIn: state.userReducer.LoggedIn,
    loadLoggedIn: state.userReducer.loadLoggedIn
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(LoginPage));
