import React, { Fragment, Component } from "react";
import { connect } from "react-redux";
import { Button, withStyles } from "@material-ui/core";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { toast } from "react-toastify";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";
import { ratesLoadRequest } from "../../../Redux/Actions/MarketDataActions/Rates/getRatesActions";
import { currenciesLoadRequest } from "../../../Redux/Actions/MarketDataActions/Currencies/getCurrenciesActions";
import {
  updateRateRequest,
  finishUpdate
} from "../../../Redux/Actions/MarketDataActions/Rates/updateRateActions";

const styles = () => ({
  container: {
    display: "flex",
    flexWrap: "wrap"
  },
  textField: {
    marginLeft: 100,
    marginRight: 100,
    width: 200
  },
  menu: {
    width: 200
  }
});

class TableManagement extends Component {
  state = {
    open: false
  };

  componentDidMount() {
    if (this.props.LoggedIn) {
      this.props.currenciesLoadRequest();
      this.props.ratesLoadRequest();
    } else {
      this.props.history.push("/loginPage");
    }
  }

  componentDidUpdate() {
    if (this.props.updateRateSuccess === true) {
      this.props.ratesLoadRequest();
      this.props.finishUpdate();
    }
  }

  handleToggle = () => {
    this.setState({ open: !this.state.open });
  };

  handleChoise = chosenRate => {
    let myRate = {};
    this.props.rates.forEach(rate => {
      if (rate.currencyName === chosenRate) {
        myRate = rate;
        myRate.isInTable = !myRate.isInTable;
      }
    });
    if (!myRate.currencyName) {
      toast("set rate for this currency please");
    } else {
      this.props.updateRateRequest(myRate);
    }
    this.handleToggle();
  };

  render() {
    const { rates, currencies } = this.props; // if the page doesnt render properly we should add loadedRates...
    return (
      <Fragment>
        <ToastContainer />
        <Fragment>
          <Button
            aria-controls="simple-menu"
            aria-haspopup="true"
            onClick={() => this.handleToggle()}
          >
            add/delete
          </Button>
          <Menu
            id="simple-menu"
            open={this.state.open}
            onClose={() => this.handleToggle()}
          >
            {currencies.map(currency => (
              <MenuItem
                key={currency.currencyName}
                onClick={() => this.handleChoise(currency.currencyName)}
              >
                {currency.currencyName}
              </MenuItem>
            ))}
          </Menu>

          <table>
            <thead>
              <tr>
                <th rowSpan="2">flag</th>
                <th rowSpan="2">currency</th>
                <th rowSpan="2">value</th>
                <th colSpan="2" scope="colgroup">
                  cash
                </th>
                <th colSpan="2" scope="colgroup">
                  transfer
                </th>
              </tr>
              <tr>
                <th>buy </th>
                <th>sell </th>
                <th>buy </th>
                <th>sell </th>
              </tr>
            </thead>
            <tbody>
              {rates.map(
                rate =>
                  rate.isInTable && (
                    <tr className="col col-lg-4 grid-wrapper" key={rate._id}>
                      <td> </td>
                      <td> {rate.currencyName}</td>
                      <td></td>
                      <td> {rate.buyCashRate}</td>
                      <td> {rate.sellCashRate}</td>
                      <td> {rate.buyTransferRate}</td>
                      <td> {rate.sellTransferRate}</td>
                    </tr>
                  )
              )}
            </tbody>
          </table>
        </Fragment>
      </Fragment>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  updateRateRequest: rate => dispatch(updateRateRequest(rate)),
  ratesLoadRequest: () => dispatch(ratesLoadRequest()),
  currenciesLoadRequest: () => dispatch(currenciesLoadRequest()),
  finishUpdate: () => dispatch(finishUpdate())
});

const mapStateToProps = state => {
  return {
    rates: state.getRatesReducer.rates,
    currencies: state.getCurrenciesReducer.currencies,
    LoggedIn: state.userReducer.LoggedIn,
    updateRateSuccess: state.updateRateReducer.updateRateSuccess,
    desiredRate: state.updateRateReducer.desiredRate
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(TableManagement));
