import { toast } from "react-toastify";
import {
  CURRENCIES_LOAD_REQUEST,
  CURRENCIES_LOAD_SUCCESS,
  CURRENCIES_LOAD_FAIL
} from "../../../Actions/MarketDataActions/Currencies/getCurrenciesActions";

const initialState = {
  currencies: [],
  loadedCurrencies: false
};

export default function(state = initialState, action) {
  switch (action.type) {
    case CURRENCIES_LOAD_REQUEST:
      return { ...state, loadedCurrencies: false };
    case CURRENCIES_LOAD_SUCCESS:
      return {
        ...state,
        loadedCurrencies: true,
        currencies: [...action.payload]
      };
    case CURRENCIES_LOAD_FAIL:
      toast("failed to upload currencies");
      return { ...state, loadedCurrencies: false };
    default:
      return state;
  }
}
