import { toast } from "react-toastify";
import {
  UPDATE_USER_SUCCESS,
  UPDATE_USER_FAIL,
  UPDATE_USER_REQUEST,
  LOAD_UPDATE_FORM
} from "../../Actions/AdminActions/updateUserActions";

const initialState = { desiredUser: {}, updateSuccess: false };

export default function(state = initialState, action) {
  switch (action.type) {
    case LOAD_UPDATE_FORM:
      return {
        ...state,
        updateSuccess: false,
        desiredUser: action.payload.user
      };
    case UPDATE_USER_REQUEST:
      return { ...state, updateSuccess: false };
    case UPDATE_USER_SUCCESS:
      return { ...state, updateSuccess: true };
    case UPDATE_USER_FAIL:
      toast("failed to update user");
      return { ...state, updateSuccess: false };
    default:
      return state;
  }
}
